// Misc.
	$misc: (
		z-index-base:				10000
	);

// Duration.
	$duration: (
		menu:						0.5s,
		transition:					0.2s
	);

// Size.
	$size: (
		element-height:				2.75em,
		element-margin:				2em,
		section-spacing:			3em,
		section-spacing-small:		1.5em,
		menu:						25em
	);

// Font.
	$font: (
		family:						('Source Sans Pro', Helvetica, sans-serif),
		family-fixed:				('Courier New', monospace),
		family-heading:				('Raleway', Helvetica, sans-serif),
		weight:						400,
		weight-bold:				700,
		weight-heading:				400,
		weight-heading-bold:		800,
		weight-heading-extrabold:	900,
		kerning-heading:			0.25em
	);

// Palette.
	$palette: (
		bg:							#ffffff,
		bg-alt:						#f4f4f4,
		fg:							#424242,
		fg-bold:					#383838,
		fg-light:					#aaaaaa,
		border:						rgba(160,160,160,0.3),
		border-bg:					rgba(160,160,160,0.075),
		border-alt:					rgba(160,160,160,0.65),
		accent:						#78909c,
		code-bg:					#282c34,
		);

// Placeholder
$ph-bg:                   #fff !default;
$ph-color:                #ced4da !default;
$ph-border-radius:        2px !default;

$ph-gutter:               30px !default;
$ph-spacer:               15px !default;

$ph-avatar-border-radius: 50% !default;